header{
    height: 100vh;
    padding-top: 4rem;
    overflow:hidden;

}

.header_container{
    text-align: center;
    height:100%;
    position:relative;
}

/* cta*/

.cta{
    margin-top:2.5rem;
    display:flex;
    gap:1.2rem;
    justify-content: center;
}

/* Social */
.header_socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap:0.8;
    position: absolute;
    left:0;
    bottom:3rem;
}

.header_socials::after{
    content:'';
    width: 1px;
    height:2rem;
    background: var(--color-primary);
}


/* Me */

.me{
    background: linear-gradient(var(--color-primary),transparent);
    width: 22rem;
    height:30rem; 
    position: absolute;
    left: calc(50% - 11rem);
    margin-top:4rem; 
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem; 
}

/* scroll down */
.scroll_down{
    position:absolute;
    right:-2.3rem;
    bottom: 5rem;
    transform:rotate(90deg);
    font-weight: 300;
    font-size:0.9rem;

}

/*medai queries(medium)*/
@media screen and (max-width:1024px) {
    header{
    height:68vh;
    }
}

/*medai queries(small)*/
@media screen and (max-width:600px) {
    header{
        height:100vh;
    }

    .header_socials,.scroll_down{
        display: none;
    }
}